import MedicineListPagination from './MedicineListPagination.vue';
import api from '../api/index';
export default {
  name: "MedicineList",

  data() {
    return {
      // display_flag : true,
      // isPhone : true,
      pagesize: 5,
      page: 1,
      totalPages: Math.ceil(this.total / this.pagesize),
      title: "My Medicines",
      // listData:null,
      listData: [{
        id: 1,
        brandname: "Norvasc" + 1,
        image: require("../assets/logo.png"),
        quantity: 2,
        validity: "03/08/2022",
        dosage: 3,
        dosagetype: "pills",
        quantitytype: "bottles"
      }, {
        id: 2,
        brandname: "Norvasc" + 2,
        image: require("../assets/logo.png"),
        quantity: 3,
        validity: "03/08/2022",
        dosage: 4,
        dosagetype: "pills",
        quantitytype: "bottles"
      }, {
        id: 3,
        brandname: "Norvasc" + 3,
        image: require("../assets/logo.png"),
        quantity: 3,
        validity: "03/08/2022",
        dosage: 4,
        dosagetype: "drops",
        quantitytype: "boxes"
      }, {
        id: 4,
        brandname: "Norvasc" + 4,
        image: require("../assets/logo.png"),
        quantity: 3,
        validity: "03/08/2022",
        dosage: 4,
        dosagetype: "capsules",
        quantitytype: "packs"
      }, {
        id: 5,
        brandname: "Norvasc" + 5,
        image: require("../assets/logo.png"),
        quantity: 3,
        validity: "03/08/2022",
        dosage: 4,
        dosagetype: "capsules",
        quantitytype: "packs"
      }]
    };
  },

  props: {
    total: {
      type: Number,
      default: 0
    },
    isSearchResult: {
      type: Boolean,
      default: false
    },
    searchResult: {
      type: Object,
      default: null
    }
  },
  components: {
    MedicineListPagination
  },

  created() {
    if (this.isSearchResult) {
      console.log("Display Search Result");
      this.title = "Search Result";
      this.listData = this.searchResult;
    } else {
      console.log("Getting pagination data");
      this.getPageData(1, this.pagesize);
    }
  },

  beforeMount() {
    this.isPhone = !(window.innerWidth > 992);
  },

  methods: {
    changePage(page, pagesize) {
      var _page = this.page,
          _pagesize = this.pagesize;
      this.page = page;
      this.pagesize = pagesize;

      if (page !== _page && pagesize || _pagesize !== pagesize) {
        console.log("get list data");
        this.getPageData(page, pagesize);
      }
    },

    getPageData(curPage, pageSize) {
      console.log("Getting page " + curPage + " data");
      var that = this;
      api.getPageData(curPage, pageSize).then(res => {
        console.log("Pagination data received");
        console.log(res.data);
        that.listData = res.data.data.records;
      });
    },

    goToMediDetail(id) {
      var mode = {
        view: 'medi_info',
        mediId: id
      };
      this.$emit("switch-event", mode);
    }

  }
};